import { Container, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core'; 

const useStyles = makeStyles({
    otsikko : {
        marginTop : 10,
        fontSize : 22,
        textAlign: "center"
    },
    alaotsikko : {
        marginTop : 10,
        fontSize : 18,
        textAlign: "center"
    },
    kappale : {
        marginTop : 10,
        fontSize : 14,
        textAlign: "center"
    }
});

function Aloitus() {

    const tyylit = useStyles();

    return (
        <Container>

            <Typography className={tyylit.otsikko}>
                Harjoitustyö: TV-sarja-sovellus
            </Typography>

            <Typography className={tyylit.alaotsikko}>
                Sovellusohjelmointi 1
            </Typography>

            <Typography className={tyylit.kappale}>
                Tämä React-projekti on Sovellusohjelmointi 1 -kurssin soveltava harjoitustyö. Työn sisältönä on sovellus, jolla käyttäjä voi hakea API-rajapinnasta televisiosarjojen tietoja, lisätä haluamansa sarjat omalle katsottavien sarjojen listalle, siirtää katsotut omalle listalleen ja lopulta tyhjentää katsottujen sarjojen listan.
            </Typography>
        </Container>
    )
}

export default Aloitus