import { Button, Container, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, Typography } from "@material-ui/core";
import { useState } from "react";

function Etsi(props) {
    const [tiedotEsiin, setTiedotEsiin] = useState(false);

    const lisaaKatsottaviin = () => {
        if(props.esitettavatTiedot.katsottu === false){
            let uusiKatsottava = props.esitettavatTiedot;
            props.setKatsottavat([...props.katsottavat, uusiKatsottava]);
        }
        else{
            let uusiKatsottava = props.esitettavatTiedot;
            props.setKatsotut([...props.katsotut, uusiKatsottava]);  
        }
      }

    return (
        <Container>

            <Typography
                style={{margin:"15px", fontSize:"1.2em"}}
                >
                Etsi sarja nimen avulla ja paina enter:
            </Typography>

            <TextField 
                label="TV-sarjan haku"
                variant="outlined"
                fullWidth={true}
                placeholder="Hae tv-sarjan tietoja sarjan nimellä ja paina enter"
                style={{marginBottom : 10}}
                onKeyPress={ (e) => {
                    if (e.key === "Enter") {
                    props.setKayttajanHakemaSarja(e.target.value);
                    props.haeTiedot(e.target.value);
                    setTiedotEsiin(true);
                    props.setKatsottavatEsiin(true);
                    e.target.value = null;
                    }
                } } 
            />

            {(!tiedotEsiin)
            ? null
            : <TableContainer style={{marginTop:"20px"}}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Kansikuva:</TableCell>
                            <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Nimi:</TableCell>
                            <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Aloitusvuosi:</TableCell>
                            <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Lopetusvuosi:</TableCell>
                            <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>IMDB:</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        <TableRow>
                        <TableCell style={{fontSize:"1em", textAlign:"center"}}><img src={props.esitettavatTiedot.kuva} alt="Säätilaa kuvaava ikoni" width={"100px"}/></TableCell>
                            <TableCell style={{fontSize:"1em", textAlign:"center"}}>{props.esitettavatTiedot.nimi}</TableCell>
                            <TableCell style={{fontSize:"1em", textAlign:"center"}}>{props.esitettavatTiedot.esitysvuosi}</TableCell>
                            <TableCell style={{fontSize:"1em", textAlign:"center"}}>{props.esitettavatTiedot.lopetusvuosi}</TableCell>
                            <TableCell style={{fontSize:"1em", textAlign:"center"}}> <a href={`https://www.imdb.com/title/${props.esitettavatTiedot.imdbID}`} target="_blank" rel="noopener">Katso sarjan tiedot IMDB:ssä</a> </TableCell>
                            <TableCell style={{fontSize:"1em", textAlign:"center"}}>
                                <Button 
                                    variant="outlined" 
                                    style={{fontSize:"0.8em"}}
                                    onClick={ () => {
                                        lisaaKatsottaviin();
                                        alert("Sarja lisätty katsottavien listaan!");
                                      } }>
                                    Lisää omalle listalle
                                </Button>
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>

            </TableContainer>
            }

        </Container>
    )
}

export default Etsi;