import { Container } from '@material-ui/core';
import {  useState } from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom';
import Aloitus from './components/Aloitus';
import Etsi from './components/Etsi';
import Listaus from './components/Listaus';
import Valikko from './components/Valikko';
import { v4 as uuid } from 'uuid';


function App() {

  const [data, setData] = useState({
    tiedot : [],
    sarjanNimi : "",
    virhe : null,
    tiedotHaettu : false
  });

  const [kayttajanHakemaSarja, setKayttajanHakemaSarja] = useState();

  const [esitettavatTiedot, setEsitettavatTiedot] = useState([]);

  const [katsottavat, setKatsottavat] = useState([]);

  const [katsottavatEsiin, setKatsottavatEsiin] = useState(false);

  const [katsotut, setKatsotut] = useState([]);

  const [katsotutEsiin, setKatsotutEsiin] = useState(false);

  const haeTiedot = async (nimi) => {
    let url = `http://api.tvmaze.com/search/shows?q=${nimi}`;


      const yhteys = await fetch(url);

      const tiedot = await yhteys.json();


      let apuData = {
                      id : uuid(),
                      nimi : tiedot[0].show.name,
                      esitysvuosi : tiedot[0].show.premiered,
                      lopetusvuosi : tiedot[0].show.ended,
                      jaksonkesto : tiedot[0].show.runtime,
                      kuva : tiedot[0].show.image.original,
                      imdbID : tiedot[0].show.externals.imdb,
                      katsottu : false
                    }

      setEsitettavatTiedot(apuData);

      console.log(apuData);

      setData({
        ...data, 
        apuData,
        tiedotHaettu : true
      });
    }


    console.log(katsottavat);
    console.log(katsotut);


  return (
    <Router>
      <Container maxWidth="md">
        <Valikko/>
        <Route path={"/"} exact>
          <Aloitus/>
        </Route>
        <Route path={"/listaus"}>
          <Listaus
            esitettavatTiedot={esitettavatTiedot}
            katsottavat={katsottavat}
            setKatsottavat={setKatsottavat}
            katsottavatEsiin={katsottavatEsiin}
            setKatsottavatEsiin={setKatsottavatEsiin}
            katsotutEsiin={katsotutEsiin}
            setKatsotutEsiin={setKatsotutEsiin}
            katsotut={katsotut}
            setKatsotut={setKatsotut}
          />
        </Route>
        <Route path={"/etsi"}>
          <Etsi
            esitettavatTiedot = {esitettavatTiedot}
            setKayttajanHakemaSarja = {setKayttajanHakemaSarja}
            haeTiedot = {haeTiedot}
            setKatsottavat = {setKatsottavat}
            katsottavat={katsottavat}
            setKatsottavatEsiin={setKatsottavatEsiin}
            setKatsotutEsiin={setKatsotutEsiin}
            katsotut={katsotut}
            setKatsotut={setKatsotut}
          />
        </Route>
      </Container>
    </Router>
  );
}

export default App;
