import { AppBar, Button, Container, Tab, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Tabs, Typography } from '@material-ui/core';
import { useState } from 'react';
import { Link } from 'react-router-dom';

function Listaus(props) {

     const [arvo, setArvo] = useState(0);


     const vaihdaSivu = (e, uusiArvo) => {
         setArvo(uusiArvo);
     }

    const vaihdaStatus = (status) => {
        status.katsottu = !status.katsottu;
    }

    const siirrettavaSarja = props.katsottavat.filter((katsottava, id) => {
        return (katsottava.id === id);
    })[0];

    const lisaaKatsottuihin = (indeksi) => {


        let katsotutApu = props.katsottavat.filter((katsottava) => {
            return (katsottava.id === props.katsottavat[indeksi].id);
        });

        console.log(katsotutApu[0]);
        vaihdaStatus(katsotutApu);

        let katsottavatApu = props.katsottavat.filter((katsottava) => {
            return (katsottava.id !== props.katsottavat[indeksi].id);
        });
        
        props.setKatsottavat(katsottavatApu);
        props.setKatsotut([...props.katsotut, katsotutApu[0]]);

        if(katsottavatApu.length <= 0){
            props.setKatsottavatEsiin(false);
            console.log(props.katsottavat.length);
        }

    }


    return (
        <AppBar position='static' color='transparent' style={{marginTop: "10px"}}>
            <Tabs value={arvo} onChange={vaihdaSivu}>
                <Tab label="Katsottavat"></Tab>
                <Tab label="Katsotut"></Tab>
            </Tabs>
            {(arvo === 0)
            ?(!props.katsottavatEsiin)
                ?<Typography
                    style={{margin:"15px"}}>
                    Sinulla ei ole vielä omalla katselulistalla sarjoja</Typography>
                :<Container>

                <Typography
                    style={{margin:"15px"}}>
                    Sarjat, jotka on merkattu katsottaviksi:
                </Typography>
                    <TableContainer style={{marginTop:"10px"}}>
                        <Table>
                            <TableHead>
                                <TableRow>
                                    <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Kansikuva:</TableCell>
                                    <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Nimi:</TableCell>
                                    <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Aloitusvuosi:</TableCell>
                                    <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Lopetusvuosi:</TableCell>
                                    <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>IMDB:</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>

                                {props.katsottavat.map((katsottava, idx) => {
                                    return(
                                    <TableRow key={idx}>
                                        <TableCell style={{fontSize:"1em", textAlign:"center"}}><img src={katsottava.kuva} alt="Sarjan kansikuva" width={"100px"}/></TableCell>
                                        <TableCell style={{fontSize:"1em", textAlign:"center"}}>{katsottava.nimi}</TableCell>
                                        <TableCell style={{fontSize:"1em", textAlign:"center"}}>{katsottava.esitysvuosi}</TableCell>
                                        <TableCell style={{fontSize:"1em", textAlign:"center"}}>{katsottava.lopetusvuosi}</TableCell>
                                        <TableCell style={{fontSize:"1em", textAlign:"center"}}> <a href={`https://www.imdb.com/title/${katsottava.imdbID}`} target="_blank" rel="noopener">Katso sarjan tiedot IMDB:ssä</a> </TableCell>
                                        <TableCell style={{fontSize:"1em", textAlign:"center"}}>
                                            <Button 
                                                key={idx}
                                                variant="outlined" 
                                                style={{fontSize:"0.8em"}}
                                                onClick={ () => {
                                                    lisaaKatsottuihin(idx);
                                                    props.setKatsotutEsiin(true);
                                                    alert("Sarja lisätty katsottujen listaan!");
                                                  } }
                                                >
                                                Siirrä katsottuihin
                                            </Button>
                                        </TableCell>
                                    </TableRow>
                                    )
                                })}
                            </TableBody>
                        </Table>
                    </TableContainer>
                    </Container>
            : null
            }
            {(arvo === 1)
            ?(!props.katsotutEsiin)
                ?<Typography
                    style={{margin:"15px"}}>
                    Sinulla ei ole vielä katsottujen sarjojen listalla sarjoja</Typography>
                :<Container>

                <Typography                        
                    style={{margin:"15px"}}>
                    Sarjat, jotka on merkattu katsotuiksi:
                </Typography>
                    <TableContainer style={{marginTop:"10px"}}>
                        <Table>
                            <TableHead>
                                <TableRow>
                                    <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Kansikuva:</TableCell>
                                    <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Nimi:</TableCell>
                                    <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Aloitusvuosi:</TableCell>
                                    <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>Lopetusvuosi:</TableCell>
                                    <TableCell style={{fontWeight:"bolder", fontSize:"1.2em", textAlign:"center"}}>IMDB:</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>

                                {props.katsotut.map((katsottu, idx) => {
                                    return(
                                    <TableRow key={idx}>
                                        <TableCell style={{fontSize:"1em", textAlign:"center"}}><img src={katsottu.kuva} alt="Sarjan kansikuva" width={"100px"}/></TableCell>
                                        <TableCell style={{fontSize:"1em", textAlign:"center"}}>{katsottu.nimi}</TableCell>
                                        <TableCell style={{fontSize:"1em", textAlign:"center"}}>{katsottu.esitysvuosi}</TableCell>
                                        <TableCell style={{fontSize:"1em", textAlign:"center"}}>{katsottu.lopetusvuosi}</TableCell>
                                        <TableCell style={{fontSize:"1em", textAlign:"center"}}> <Link href={`https://www.imdb.com/title/${katsottu.imdbID}`} target="_blank" rel="noopener">Katso sarjan tiedot IMDB:ssä</Link> </TableCell>
                                    </TableRow>
                                    )
                                })}
                            </TableBody>
                        </Table>
                    </TableContainer>
                    <Button 
                        style={{margin:"10px"}}
                        variant="outlined" 
                        color='primary'
                        fullWidth
                        onClick={ () => {
                            props.setKatsotut([]);
                            props.setKatsotutEsiin(false);
                          } }
                        >
                        Poista katsottujen sarjojen tiedot
                    </Button>
                    </Container>
            : null
            }
        </AppBar>
    )
}

export default Listaus;